* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.main_div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: grey;
  background-color: rgb(228, 181, 181);
}

.App {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 380px;
  height: 300px;
  box-shadow: 20px 20px 10px rgb(153, 94, 94);
  border-radius: 5px 70px 5px 70px;
}

select {
  width: 50px;
  border: none;
}
